.backdrop {
  position: fixed;

  // position: relative;

  width: 250px;
  height: 100vh;
  background-color: #e17992;
  transition: opasity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  top: 0;
  right: 0;
  z-index: 10;
}
.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.mob-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mob-btn-close {
  position: absolute;
  top: 16px;
  right: 34px;
  fill: var(--primary-text-color);
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.mob-btn-main {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 186px;
  height: 44px;

  font-family: var(--primary-font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21 / 16);
  border-radius: 22px;
  border: 0;
  color: var(--main-accent-color);
  background: var(--white-bg);

  margin-top: 60px;
  margin-bottom: 19px;
  cursor: pointer;
  @media screen and (min-width: 720px) {
    margin-top: 53px;
    margin-bottom: 32px;
  }
}

.mob-btn-simbol {
  margin-left: 14px;
}
.mob-menu-list {
  margin-left: 21px;
}
.mob-menu-item {
  // margin-bottom: 23px;
}
.link-top {
  display: block;
  width: 100%;
  font-family: var(--primary-font-family);
  font-weight: bold;
  font-size: 14px;
  line-height: calc(18 / 14);
  color: var(--primary-text-color);
  // border: 1px solid #E18298;
  padding-bottom: 12px;
  padding-top: 11px;
  border-top: 1px solid #e18298;
}
.mob-menu-link {
  display: block;
  width: 100%;
  font-family: var(--primary-font-family);
  font-weight: bold;
  font-size: 14px;
  line-height: calc(18 / 14);
  color: var(--primary-text-color);
  // border: 1px solid #E18298;
  padding-bottom: 12px;
  padding-top: 11px;
  border-bottom: 1px solid #e18298;
}
.mob-menu-link:hover,
.mob-menu-link:focus {
  color: var(--main-accent-color);
}
